.giz-data-grid-wrapper {
    position: relative;
    overflow: auto;

    .giz-menu {
        position: absolute !important;
    }
}

.giz-data-grid {
    position: relative;
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 0.2rem;
    color: $gray-black-theme-dark;
    font-size: $font-16-theme-dark;
    background: $dark-gray-theme-dark;

    & > thead td, & > thead th {
        height: 5rem;
        text-transform: $text-uppercase;
        font-size: $font-15-theme-dark;
        color: $gray-black-theme-dark;
        font-weight: $font-weight-400;
        padding: 0 0.5rem;
        text-align: left;

        .giz-data-grid-header {
            display: flex;
            height: 100%;
            align-items: center;
        }

        &.is-sorted-up {
            position: relative;

            &:after {
                content: '\f0de';
                position: absolute;
                right: 1rem;
                height: 100%;
                display: flex;
                align-items: $align-center;
                justify-content: $justify-center;
                top: 0;
            }
        }

        &.is-sorted-down {
            position: relative;

            &:after {
                content: '\f0dd';
                position: absolute;
                right: 1rem;
                height: 100%;
                display: flex;
                align-items: $align-center;
                justify-content: $justify-center;
                top: 0;
            }
        }
    }

    & > tbody > tr:not(.table__details) {
        height: 3.9rem;
        background: transparent;
        border-radius: $rounded-lg-theme-dark;
        margin-bottom: $spacer-12;

        & > td {
            border-top: 1px solid transparent;
            border-bottom: 1px solid transparent;

            &:first-child {
                border-left: 1px solid transparent;
            }

            &:last-child {
                border-right: 1px solid transparent;
            }
        }

        &:hover {
            background: $dark-jungle-theme-dark;
            color: white;

            & > td {
                border-top: 1px solid $onyx-black-theme-dark;
                border-bottom: 1px solid $onyx-black-theme-dark;

                &:first-child {
                    border-left: 1px solid $onyx-black-theme-dark;
                }

                &:last-child {
                    border-right: 1px solid $onyx-black-theme-dark;
                }
            }
        }

        &.is-opened {
            border-color: #453855;
            background: $primary-color-theme-dark;
            cursor: pointer;
            color: white;

            & > td {
                border-top: 1px solid $onyx-black-theme-dark;
                border-bottom: 1px solid $onyx-black-theme-dark;

                &:first-child {
                    border-left: 1px solid $onyx-black-theme-dark;
                }

                &:last-child {
                    border-right: 1px solid $onyx-black-theme-dark;
                }
            }
        }

        &.is-selected {
            background: $dark-navy-theme-dark;
            cursor: pointer;

            & > td {
                border-top: 1px solid $onyx-black-theme-dark;
                border-bottom: 1px solid $onyx-black-theme-dark;

                &:first-child {
                    border-left: 1px solid $onyx-black-theme-dark;
                }

                &:last-child {
                    border-right: 1px solid $onyx-black-theme-dark;
                }
            }

            &:hover {
                background: rgba(90,103,242,0.2);

                & > td {
                    border-top: 1px solid $onyx-black-theme-dark;
                    border-bottom: 1px solid $onyx-black-theme-dark;

                    &:first-child {
                        border-left: 1px solid $onyx-black-theme-dark;
                    }

                    &:last-child {
                        border-right: 1px solid $onyx-black-theme-dark;
                    }
                }
            }
        }

        &.is-active {
            border-color: #453855;
            background: $dark-light-color-theme-dark;
            border-radius: 4px 4px 0 0;
            margin-bottom: $spacer-0;
        }

        & > td:first-child {
            border-radius: 4px 0 0 4px;
        }

        & > td:last-child {
            border-radius: 0 4px 4px 0;
        }
    }

    & > tbody > tr > td {
        vertical-align: middle;
        padding: 0 0.5rem;

        .giz-input-control {
            margin: 0 -0.5rem;
        }
    }

    & > tbody > tr.table__details > td {
        padding: $spacer-0;
    }

    &-check {
        width: 1%;
    }
}

.column-selector-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
}

.column-selector {
    top: 1.2rem;
    right: 0;

    .giz-checkbox {
        label {
            margin: 0;
        }
    }
}

.table__details {
    display: none;
    background: transparent;

    &:hover {
        & > td {
            border-color: transparent;
        }
    }

    .table__row-dropdown.is-opened + & {
        display: table-row;
    }
}

.giz-data-grid__details-content {
    position: relative;
    width: 100%;
    border: 1px solid #292e39;
    border-radius: $rounded-theme-dark;
    display: flex;
    align-items: $align-start;
    box-sizing: border-box;
    border-top: $border-0-theme-dark;
    padding: 1.2rem 0;
    margin-top: $spacer-n12;
}

.giz-data-grid-sticky-header {
    max-height: 100%;

    .column-selector-wrapper {
        position: sticky;
    }

    .giz-data-grid {

        & > thead td, & > thead th {
            position: sticky;
            top: 0;
            z-index: 1;
            color: $gray-black-theme-dark;
            background: $dark-gray-theme-dark;
        }
    }
}
