//=============== gizmo chip classes ================//

.giz-chip {
    display: inline-flex;
    align-items: center;
    padding: 9px 12px;
    height: 40px;
    color: #3F8CFF;
    background: #0B1A24;
    border-radius: 6px;
    //cursor: pointer;
    @include font-m($font-weight-light);

    .giz-icon-button {
        margin-left: 10px;

        svg {
            color: #0B1A24;
        }
    }
}